"use client";

import { cn } from "@/lib/utils";
import {
  Home,
  Plus,
  List,
  Calendar,
  Clock,
  Bookmark,
  Search,
  Settings,
} from "lucide-react";
import Link from "next/link";

const mainIcons = [
  { href: "/dashboards", icon: Home, label: "Home" },
  { href: "/dashboards/new", icon: Plus, label: "Add" },
  { href: "/settings", icon: Settings, label: "Settings" },
  { href: "#", icon: List, label: "List" },
  { href: "#", icon: Calendar, label: "Calendar" },
  { href: "#", icon: Clock, label: "Clock" },
  { href: "#", icon: Bookmark, label: "Bookmark" },
] as const;

type SidebarProps = {
  activeHref?: string;
  className?: string;
  showFamilyHub?: boolean;
};

export function Sidebar({
  activeHref,
  className,
  showFamilyHub = true,
}: SidebarProps) {
  return (
    <aside
      className={cn(
        "flex w-14 flex-col items-center gap-2 rounded-full bg-gray-900/90 py-3 dark:bg-black/40",
        className
      )}
      aria-label="Sidebar"
    >
      {mainIcons.map(({ href, icon: Icon, label }) => {
        const isActive = activeHref === href;
        return (
          <Link
            key={label}
            href={href}
            aria-label={label}
            className={cn(
              "flex h-10 w-10 items-center justify-center rounded-full transition-all duration-150",
              isActive
                ? "bg-white text-gray-900"
                : "text-white/80 hover:bg-white/15 hover:text-white"
            )}
          >
            <Icon className="h-5 w-5" />
          </Link>
        );
      })}
      {showFamilyHub && (
        <>
          <div className="my-1 h-px w-6 bg-white/20" />
          <span className="px-2 text-center text-[10px] font-medium text-white/60">
            Smart Family Hub
          </span>
          <button
            type="button"
            aria-label="Search"
            className="flex h-9 w-9 items-center justify-center rounded-full text-white/80 hover:bg-white/15 hover:text-white"
          >
            <Search className="h-4 w-4" />
          </button>
          <div className="flex -space-x-2">
            {[1, 2, 3].map((i) => (
              <div
                key={i}
                className="h-7 w-7 rounded-full border-2 border-gray-900 bg-gray-500 dark:border-dark-page"
                aria-hidden
              />
            ))}
          </div>
        </>
      )}
    </aside>
  );
}
