"use client";

import { AppShell } from "@/components/layout/app-shell";
import { GlassCard } from "@/components/layout/glass-card";
import Link from "next/link";
import { useQuery, useQueryClient } from "@tanstack/react-query";
import { Trash2 } from "lucide-react";

type DashboardItem = { id: string; name: string; theme: string; createdAt: string };

export default function DashboardsPage() {
  const queryClient = useQueryClient();
  const { data: dashboards = [], isLoading } = useQuery<DashboardItem[]>({
    queryKey: ["dashboards"],
    queryFn: async () => {
      const res = await fetch("/api/dashboards");
      if (!res.ok) throw new Error("Failed to load");
      return res.json();
    },
  });

  async function handleDelete(dashboardId: string, name: string) {
    if (!confirm(`Dashboard "${name}" verwijderen?`)) return;
    const res = await fetch(`/api/dashboards/${dashboardId}`, { method: "DELETE" });
    if (!res.ok) return;
    queryClient.invalidateQueries({ queryKey: ["dashboards"] });
  }

  return (
    <AppShell activeTab="/dashboards">
      <div className="space-y-4">
        <div className="flex items-center justify-between">
          <h2 className="text-xl font-semibold">Dashboards</h2>
          <Link
            href="/dashboards/new"
            className="rounded-full bg-accent-yellow dark:bg-accent-green px-4 py-2 text-sm font-medium text-gray-900"
          >
            New dashboard
          </Link>
        </div>
        {isLoading ? (
          <p className="text-sm text-gray-500">Loadingâ€¦</p>
        ) : dashboards.length === 0 ? (
          <GlassCard>
            <p className="text-sm text-gray-600 dark:text-gray-400">
              No dashboards yet.{" "}
              <Link href="/onboarding" className="text-accent-purple dark:text-accent-green underline">
                Start onboarding
              </Link>{" "}
              or{" "}
              <Link href="/dashboards/new" className="underline">create one</Link>.
            </p>
          </GlassCard>
        ) : (
          <ul className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
            {dashboards.map((d) => (
              <li key={d.id} className="relative group">
                <Link href={`/dashboards/${d.id}`} className="block">
                  <GlassCard className="block hover:shadow-lg transition-shadow pr-10">
                    <h3 className="font-medium">{d.name}</h3>
                    <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                      Theme: {d.theme}
                    </p>
                  </GlassCard>
                </Link>
                <button
                  type="button"
                  onClick={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    handleDelete(d.id, d.name);
                  }}
                  className="absolute right-2 top-2 p-1.5 rounded-full text-gray-400 hover:text-red-500 hover:bg-red-500/10 transition-colors"
                  aria-label={`${d.name} verwijderen`}
                >
                  <Trash2 className="h-4 w-4" />
                </button>
              </li>
            ))}
          </ul>
        )}
      </div>
    </AppShell>
  );
}
